@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&family=Roboto:wght@700&display=swap";
:root {
  --primaryColor: #222;
  --primaryLightColor: rgba(34, 34, 34, .75);
  --btn-Color: #2e4dec;
  --btn-hoverColor: #1abc9c;
  --btn-disabledColor: #ababab;
  --backgroundColor: #fff;
  --extrabackgroundColor: #fcfcfc;
  --accentColor: #f8d24e;
  --textColor: #818181;
  --extraColor: #d6936d;
  --copyRightColor: #7a7a7a;
  --blackColor: #000;
  --whiteColor: #fff;
  --borderColor: #e1e1e1;
  --transitionDuration: 250ms;
  --transitionTimingFunction: cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--primaryColor);
  background-color: var(--extrabackgroundColor);
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

picture, img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  height: auto;
  max-width: 390px;
  outline: 1px solid #dc143c;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    max-width: 1200px;
  }
}

.btn {
  text-transform: uppercase;
  color: var(--whiteColor);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  position: relative;
}

.header {
  background-color: var(--blackColor);
}

.header__logo-link {
  height: 20px;
  transition: all .3s var(--transitionTimingFunction);
  margin: 0;
  padding: 0;
}

.header__logo-link:hover, .header__logo-link:focus {
  outline: none;
}

.header__logo-link:hover .header__logo, .header__logo-link:focus .header__logo {
  fill: var(--btn-hoverColor);
  scale: 1.2;
}

.header__logo {
  fill: var(--whiteColor);
  transition: all .3s var(--transitionTimingFunction);
}

.header__menu-btn {
  color: var(--whiteColor);
  transition: all .3s var(--transitionTimingFunction);
  font-size: 25px;
}

@media screen and (min-width: 1200px) {
  .header__menu-btn {
    display: none;
  }
}

.header__menu-btn:hover, .header__menu-btn:focus {
  color: var(--btn-hoverColor);
  outline: none;
  scale: 1.2;
}

.header__container {
  background-color: var(--blackColor);
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .header__container {
    gap: 50px;
  }
}

.header__nav {
  display: none;
}

@media screen and (min-width: 1200px) {
  .header__nav {
    justify-content: space-evenly;
    align-items: center;
    gap: 100px;
    display: flex;
  }
}

.header__nav-link {
  text-transform: uppercase;
  color: var(--whiteColor);
  transition: all .3s var(--transitionTimingFunction);
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
}

.header__nav-link:hover, .header__nav-link:focus {
  color: var(--btn-hoverColor);
  outline: none;
}

.header__search-btn {
  transition: all .3s var(--transitionTimingFunction);
  font-size: 30px;
  line-height: 30px;
}

@media screen and (max-width: 1199px) {
  .header__search-btn {
    display: none;
  }
}

.header__search-btn:hover, .header__search-btn:focus {
  color: var(--btn-hoverColor);
  outline: none;
  scale: 1.2;
}

